import React from 'react';
import styled from 'styled-components';

const InfoWrapper = styled.dl``;

const DDcv = styled.dd`
  width: 600px;
  float: right;
`;

const DDclear = styled(DDcv)`
  float: none;
  margin: 0;
  height: 15px;
`;

const DTcv = styled.dt`
  font-style: italic;
  font-weight: bold;
  font-size: 18px;
  text-align: right;
  padding: 0 26px 0 0;
  width: 150px;
  float: left;
  height: 100%;
  border-right: 1px solid #555;
`;

const ULcv = styled.ul`
  margin: 0 0 32px 17px;
`;

const H2span = styled.span`
  bottom: 0;
  right: 0;
  font-style: italic;
  font-family: Georgia, Serif;
  font-size: 16px;
  color: #555;
  font-weight: normal;
`;

const Info = ({ title, lines, isList }) => {
  console.log(lines);
  return (
    <div>
      <DTcv>{title}</DTcv>
      <DDcv>
        {lines.map((line) => (
          <div>
            <h2>
              {line.title}
              <H2span>{line.titleSpan}</H2span>
            </h2>
            {isList ? (
              <ULcv>
                {line.points.map((point) => (
                  <li>l{point}</li>
                ))}
              </ULcv>
            ) : (
              <>
                {line.points.map((point) => (
                  <p>
                    {point.textFront}
                    <H2span>{point.span}</H2span>
                    {point.textBehind}
                  </p>
                ))}
              </>
            )}
          </div>
        ))}
      </DDcv>
    </div>
  );
};

const InfoLinks = ({ title, lines, isList }) => {
  console.log(lines);
  return (
    <div>
      <DTcv>{title}</DTcv>
      <DDcv>
        <ULcv>
          {lines.map((line) => (
            <li>
              <a href={line.link}>{line.linkText}</a>
              {line.text}
            </li>
          ))}
        </ULcv>
      </DDcv>
    </div>
  );
};

const InfoSmall = ({ title, text }) => {
  return (
    <div>
      <DTcv>{title}</DTcv>
      <DDcv>{text}</DDcv>
    </div>
  );
};

export {
  InfoWrapper,
  Info,
  InfoSmall,
  InfoLinks,
  DDclear,
  DDcv,
  DTcv,
  ULcv,
  H2span,
};
